/* Global modal styles to ensure the overlay renders correctly even outside .work-order-content */
.modal-overlay {
  position: fixed;
  inset: 0;
  background: rgba(0, 0, 0, 0.45);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

.modal {
  width: min(720px, 95vw);
  max-height: 80vh;
  background: #ffffff;
  border-radius: 0.75rem;
  border: 1px solid #e5e7eb;
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.2);
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.modal-header {
  padding: 1.5rem 1.5rem 1rem;
  border-bottom: 1px solid #e2e8f0;
  display: flex;
  align-items: center;
  gap: 0.75rem;
}

.modal-body {
  padding: 1rem 1.25rem;
  overflow: auto;
}

.modal-footer {
  padding: 1rem 1.5rem 1.5rem;
  border-top: 1px solid #e2e8f0;
  display: flex;
  justify-content: flex-end;
  gap: 1rem;
}

.btn {
  padding: 0.75rem 1.5rem;
  border-radius: 8px;
  font-size: 0.875rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;
  border: none;
}

.btn.btn-secondary {
  background: white;
  color: #374151;
  border: 1px solid #d1d5db;
}

.btn.btn-secondary:hover {
  background: #f9fafb;
  border-color: #9ca3af;
}

.btn.btn-primary {
  background: #2563eb;
  color: white;
}

.btn.btn-primary:hover {
  background: #1d4ed8;
}

/* Timeline styles inside modal (match the card timeline UI) */
.modal .timeline-item {
  position: relative;
  display: flex;
  align-items: flex-start;
  gap: 1rem;
  margin-bottom: 1.25rem;
  padding-left: 1rem;
}

.modal .timeline-item::before {
  content: "";
  position: absolute;
  left: 0.375rem;
  top: 0.25rem;
  bottom: -0.25rem;
  width: 2px;
  background: #e5e7eb;
}

.modal .timeline-item .timeline-dot {
  width: 0.625rem;
  height: 0.625rem;
  background-color: #2563eb; /* primary */
  border-radius: 50%;
  flex-shrink: 0;
  margin-top: 0.25rem;
  border: 2px solid #ffffff;
  box-shadow: 0 0 0 2px #e5e7eb;
}

.modal .timeline-item .timeline-dot.dot-muted {
  background-color: #9ca3af;
}

.modal .timeline-item .timeline-content {
  flex: 1;
}

.modal .timeline-item .timeline-content label {
  display: block;
  font-size: 0.875rem;
  font-weight: 600;
  color: #374151;
  margin-bottom: 0.25rem;
}

.modal .timeline-item .timeline-content p {
  margin: 0;
  color: #6b7280;
  font-size: 0.875rem;
}

/* Modal: assignment list styles to mirror the card UI */
.modal .assignment-list {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
}

.modal .assignment-row {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0.75rem;
  border: 1px solid #e5e7eb;
  border-radius: 0.5rem;
  background: #fafafa;
}

.modal .assignment-row.selectable {
  cursor: pointer;
}

.modal .assignment-row.selectable:hover {
  background: #f5f5f5;
}

.modal .assignment-left {
  display: flex;
  align-items: center;
  gap: 0.75rem;
}

.modal .avatar-circle {
  width: 2rem;
  height: 2rem;
  border-radius: 9999px;
  background: #e5e7eb;
  color: #374151;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  font-weight: 600;
}

.modal .assignment-meta {
  display: flex;
  flex-direction: column;
}

.modal .assignment-name {
  font-weight: 600;
  color: #111827;
}

.modal .assignment-sub {
  font-size: 0.75rem;
  color: #6b7280;
}

.modal .assignment-actions {
