  border: 1px solid #e5e7eb;
}

.modal .pill.muted {
  background: #f3f4f6;
  color: #374151;
}

.modal .icon-btn {
  background: transparent;
  border: none;
  cursor: pointer;
  padding: 0.25rem;
  border-radius: 0.375rem;
}

.modal .icon-btn.danger {
  color: #b91c1c;
}

.modal .icon-btn.danger:hover {
  background: #fee2e2;
